












































































































































import {Component, Vue} from "vue-property-decorator";


import {issueUpdated, shippingUpdatedAll, ShippingUpdatedPayload} from "@/graphql/subscription";
import {updatedIssue, updatedIssuePayload} from "@/graphql/mutations";
import {State} from "vuex-class";


@Component
export default class Issue extends Vue {
  @State(state => state.profile.profile) profile: any


  typeUserName(type: String){
    switch (type) {
      case "Dboys":
        return "Repartidor"
      case "User":
        return "Usuario"
    }
  }


  updatedStatusIssue(id: string, status: string){
    const vars = new updatedIssuePayload(id, status, this.profile.id);
    this.$apollo.mutate(
        {
          mutation: updatedIssue,
          variables: vars.response()

        }
    ).then(()=> {
      this.$alert("Se ha actualizado el issue", "¡Exito!", "success")
    }).catch(()=>{
      this.$alert("No se ha actualizado el issue", "¡Error!", "error")

    });
  }

  created(){
    const observer = this.$apollo.subscribe({
      query: issueUpdated,
    })
    observer.subscribe({
      next (data) {
        console.log(data)
      },
      error (error) {
        console.log(error)
      },
    })
  }

}
